html {
  scroll-behavior: smooth;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
  display: block;
}

.container {
  min-width: 320px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

body {
  color: var(--main-white-color);
  font-family: PT Serif, serif;
  font-size: 18px;
  line-height: 3.22;
  overflow-x: hidden;
}

button {
  outline: none;
  font-family: PT Serif, serif;
}

a {
  outline: none;
}

.header {
  width: 100%;
  height: 47px;
  background-color: #181818;
  padding-top: 15px;
  padding-left: 15px;
  box-shadow: 0 4px 4px #00000040;
}

.header__btn {
  width: 24px;
  height: 17px;
  position: relative;
}

.header__btn--line {
  width: 24px;
  height: 3px;
  background-color: var(--modal-btn-color);
  transition: transform .25s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__btn--line:nth-of-type(2) {
  top: calc(50% - 7px);
}

.header__btn--line:nth-of-type(3) {
  top: calc(50% + 7px);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-white-color: #fff;
  --accent-color: #ffc700;
  --accent-green-color: #3bde7c;
  --accent-red-color: #eb1b00;
  --modal-active-link: #f4ce0c;
  --about-dark-color: #272727;
  --footer-dark-color: #181818;
  --modal-btn-color: #dadada;
  --answer-bg-color: #f2f3f3;
  --disabled-btn-text: #8e8e8e;
  --disabled-btn-bgc: #dadada;
  --active-btn-text: #0d0c11;
  --active-item-text: #272727;
  --active-item-color: #2950c2;
  --copyright-color: #ffffff80;
}

